(function() {
  'use strict';

  angular
    .module('wenderPortfolio', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'restangular', 'ui.router']);

})();

(function() {
  'use strict';

  angular
      .module('wenderPortfolio')
      .service('webDevTec', webDevTec);

  /** @ngInject */
  function webDevTec() {
    var data = [
      {
        'title': 'Angular Simple Input Mask',
        'url': 'http://wender.github.io/angular-simple-input-mask/',
        'description': 'Way simple Angular directive to apply mask to input fields also with dynamic/multiple masks to the same field, which means that you can have different masks for different lengths of numbers',
        'logo': 'angular.png'
      },
      {
        'title': 'Angular Creditcard Flag',
        'url': 'http://wender.github.io/angular-cardflag/',
        'description': 'This directive will look for attribute "creditcard-flag" on an input field, will add a class card-flag and a SPAN with a class "_flag" after it, when you start typing a number, it will add a class corresponding to the Credit Card brand.',
        'logo': 'browsersync.png'
      },
      {
        'title': 'Angular Animated Label',
        'url': 'http://wender.github.io/angular-animated-label/',
        'description': 'Awesome effect to save space at forms turning labels into placeholders and still make it\'s information visible',
        'logo': 'gulp.png'
      },
      {
        'title': 'Simple Accordion',
        'url': 'http://wender.github.io/angular-simple-accordion/',
        'description': 'Way simple Angular directive to turn any serial elements into an accordion, such as UL > LI',
        'logo': 'jasmine.png'
      },
      {
        'title': 'Slide Wrapper',
        'url': 'http://wender.github.io/angular-slide-wrapper/',
        'description': 'Turn any list into a slide/carousel, I hope this directive can help you to solve your problem, it\'s a simple solution when you need a Slide/Carousel whithout having to configure tons of things, just take care of your CSS, let this directive solve the problem of making it work well on desktop or mobile.',
        'logo': 'karma.png'
      }
    ];

    this.getTec = getTec;

    function getTec() {
      return data;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('wenderPortfolio')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1446917356489;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }


    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('wenderPortfolio')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('wenderPortfolio')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('about', {
        url: '/about',
        templateUrl: 'app/about/about.html'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global moment:false */
(function() {
  'use strict';

  angular
    .module('wenderPortfolio')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('wenderPortfolio')
    .config(config);

  /** @ngInject */
  function config($locationProvider, $logProvider) {
    $locationProvider.html5Mode(true);

    // Enable log
    $logProvider.debugEnabled(true);


  }

})();

